<template>
  <div style="margin-top: 30px; text-align:center" v-loading="loading">
    <div>
      <el-image
        style="width: 90px; height: 90px; border-radius: 50%; object-fit:cover"
        :src="userHeaderImg"
        fit="scale-down"
        @click="modifyPortraitDialogVisible = true"
      >
      </el-image>
    </div>
    <label
      style="color: #666666; font-size: 30px; font-weight: bold; margin-top: 16px; margin-bottom: 19px"
      >个人资料</label
    >
    <hr
      style="width: 650px; height: 1px; background-color: #DFDFDF; border: none; margin-bottom: 34px"
    />
    <div style="margin:0 auto;display:inline-block;text-align:center">
      <label class="style_text_title">用户名&nbsp; &nbsp;</label>
      <label class="style_text_vlaue">{{ username }}</label>
      <label
        slot="append"
        class="style_modify_button"
        @click="modifyUserNameDialogVisible = true"
        >修改</label
      >
    </div>
    <div
      style="margin:0 auto;display:inline-block;text-align:center"
    >
      <label class="style_text_title">登录账号</label
      ><label class="style_text_vlaue">{{ loginName }}</label>
      <label
        slot="append"
        class="style_modify_phone_button"
        @click="modifyPhoneDialogVisible = true">{{ changePhoneBtText }}</label>
    </div>
    <div>
      <el-button
        @click="modifyPasswordDialogVisible = true"
        class="style_sure_button"
        >修改密码</el-button
      >
    </div>
    <div class="divider"></div>
    <!--修改密码对话框-->
    <el-dialog
      title="修改密码"
      class="style_dialog"
      :show-close="true"
      :visible.sync="modifyPasswordDialogVisible"
      :close-on-click-modal="clickSpaceCloseDialog"
      :before-close="beforeCloseDialog"
    >
      <el-form
        ref="changePasswordDialogFormData"
        class="style_dialog_content"
        :model="changePasswordDialogFormData"
        :rules="loginRules"
        autocomplete="on"
        label-position="left"
      >
        <el-form-item prop="phoneNum">
          <el-input
            maxlength="11"
            placeholder="请输入用户手机号"
            prefix-icon="el-icon-mobile-phone"
            v-model="changePasswordDialogFormData.phoneNum"
            name="phoneNum"
            type="phone"
            tabindex="1"
            @keyup.enter.native="checkModifyPassword"
            v-on:input="onPhoneInput"
          />
        </el-form-item>
        <el-tooltip
          v-model="capsTooltip"
          content="Caps lock is On"
          placement="right"
          manual
        >
          <el-form-item prop="verifyCode" >
            <el-input
              placeholder="请输入手机验证码"
              prefix-icon="el-icon-message"
              v-model="changePasswordDialogFormData.verifyCode"
              name="verifyCode"
              tabindex="2"
              @keyup.enter.native="checkModifyPassword"
              v-on:input="onInput"
            >
              <el-button
                slot="append"
                :disabled="sendVerifyCodeDisable"
                v-model="verifyCodeButtonTitle"
                @click.native.prevent="sentChangePasswordVerifyCode"
              >
                {{ verifyCodeButtonTitle }}
              </el-button>
            </el-input>
          </el-form-item>
        </el-tooltip>

        <el-tooltip
          v-model="capsTooltip"
          content="Caps lock is On"
          placement="right"
          manual
        >
          <el-form-item prop="password">
            <el-input
              placeholder="请输入密码"
              prefix-icon="el-icon-lock"
              :key="passwordType"
              ref="password"
              v-model="changePasswordDialogFormData.password"
              :type="passwordType"
              name="password"
              tabindex="3"
              autocomplete="on"
              @keyup.native="checkCapslock"
              @blur="capsTooltip = false"
              @keyup.enter.native="checkModifyPassword"
              v-on:input="onInput"
            />
          </el-form-item>
        </el-tooltip>
        <div>
          <el-button
            class="style_dialog_sure_button"
            :disabled="sureButtonDisable"
            @click.native.prevent="checkModifyPassword"
            >确定</el-button
          >
        </div>
      </el-form>
    </el-dialog>

    <!--换绑手机号对话框-->
    <el-dialog
      :title="changePhoneBtText"
      class="style_dialog"
      :visible.sync="modifyPhoneDialogVisible"
      :show-close="true"
      :close-on-click-modal="clickSpaceCloseDialog"
      :before-close="beforeCloseDialog"
    >
      <el-form
        ref="changePhoneDialogFormData"
        :model="changePhoneDialogFormData"
        :rules="loginRules"
        class="style_dialog_content"
        autocomplete="on"
        label-position="left"
      >
        <el-form-item prop="phoneNum">
          <el-input
            maxlength="11"
            placeholder="请输入用户手机号"
            prefix-icon="el-icon-mobile-phone"
            v-model="changePhoneDialogFormData.phoneNum"
            name="phoneNum"
            type="phone"
            tabindex="1"
            @keyup.enter.native="checkModifyPhone"
            v-on:input="onPhoneInput"
          />
        </el-form-item>

        <el-tooltip
          v-model="capsTooltip"
          content="Caps lock is On"
          placement="right"
          manual
        >
          <el-form-item prop="verifyCode">
            <el-input
              placeholder="请输入手机验证码"
              prefix-icon="el-icon-message"
              v-model="changePhoneDialogFormData.verifyCode"
              name="verifyCode"
              tabindex="2"
              @keyup.enter.native="checkModifyPhone"
              v-on:input="onInput"
            >
            <el-button
                :disabled="sendVerifyCodeDisable"
                v-model="verifyCodeButtonTitle"
                slot="append"
                @click.native.prevent="sentChangePhoneVerifyCode"
                >{{ verifyCodeButtonTitle }}</el-button>
            </el-input>
          </el-form-item>
        </el-tooltip>
        <div>
          <el-button
            class="style_dialog_sure_button"
            @click.native.prevent="checkModifyPhone"
            :disabled="sureButtonDisable"
            >确定</el-button
          >
        </div>
      </el-form>
    </el-dialog>
    <!--修改用户名对话框-->
    <el-dialog
      title="修改用户名"
      class="style_dialog"
      :visible.sync="modifyUserNameDialogVisible"
      :show-close="true"
      :close-on-click-modal="clickSpaceCloseDialog"
      :before-close="beforeCloseDialog"
    >
      <el-form
        ref="chnageNameDialogFormData"
        :model="chnageNameDialogFormData"
        :rules="loginRules"
        class="style_dialog_content"
        autocomplete="on"
        label-position="left"
      >
        <el-form-item prop="username">
          <div>
            <label style="width: 8px;">用户名：</label>
            <el-input
              style="width: 100%"
              ref="username"
              maxlength="11"
              placeholder="请输入新用户名"
              v-model="chnageNameDialogFormData.username"
              type="text"
              name="username"
              tabindex="1"
              @keyup.enter.native="checkModifyUserName"
              v-on:input="onInput"
            />
          </div>
        </el-form-item>
        <div>
          <el-button
            class="style_dialog_sure_button"
            @click.native.prevent="checkModifyUserName"
            :disabled="sureButtonDisable"
          >
            确定
          </el-button>
        </div>
      </el-form>
    </el-dialog>
    <!--修改头像弹窗-->
    <el-dialog
      title="修改头像"
      class="style_dialog_portrait"
      :visible.sync="modifyPortraitDialogVisible"
      :show-close="true"
      :close-on-click-modal="clickSpaceCloseDialog"
      :before-close="beforeCloseDialog"
    >
      <el-form>
            <img v-for="item in portraitData" :key="item.id" :src="item.url" alt="" @click="portraitClick(item.id)" style="width:45px; height:45px; margin:12px">
        <div>
          <el-button
            class="style_dialog_sure_button"
            @click="updatePortrait"
          >
            确定
          </el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import event from '../../../event.js'
import header from '../../../assets/images/1.jpg'
import {uploadFile, ossUpload, updateUserInfo, sentPhoneVerifyCode, obtainPortraitData} from '../../../service/SystemService.js'
import { getLoginInfo, saveLoginInfo } from '../../../assets/common/utils.js'
// , saveLoginInfo, saveValueLoginInfo getValueFromLoginInfo,
export default {
  data () {
    const validateUsername = (rule, value, callback) => {
      if (!value) {
        callback(new Error('用户名不能为空'))
      } else {
        callback()
      }
    }
    const validatePhone = (rule, value, callback) => {
      var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
      if (!value) {
        callback(new Error('手机号不能为空'))
      } else if (!reg.test(value)) {
        this.toastWarning('手机号格式不正确')
      } else {
        callback()
      }
    }
    const validateCode = (rule, value, callback) => {
      if (!value) {
        callback(new Error('验证码不能为空'))
      } else {
        callback()
      }
    }
    const validatePassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error('密码不能为空'))
      } else if (value.length < 6) {
        callback(new Error('密码大于六位'))
      } else {
        callback()
      }
    }
    return {
      messageOffset: 0,
      startCountTimer: false,
      sendVerifyCodeDisable: true,
      sureButtonDisable: true,
      countDownTime: 0,
      verifyCodeButtonTitle: '发送验证码',
      chnageNameDialogFormData: {
        username: ''
      },
      changePhoneDialogFormData: {
        phoneNum: '',
        verifyCode: ''
      },
      changePasswordDialogFormData: {
        phoneNum: '',
        verifyCode: '',
        password: ''
      },
      relativeUserHeaderImg: getLoginInfo('url'),
      userHeaderImg: header,
      username: getLoginInfo('real_name', ''),
      userphone: getLoginInfo('user_name'),
      loginName: getLoginInfo('user_name'),
      hasPhone: (getLoginInfo('user_name') !== '' && getLoginInfo('user_name') !== null),
      changePhoneBtText: '',
      loading: false,
      upload_headers: { 'Authentication-token': getLoginInfo('Authentication-token') },
      loginRules: {
        username: [
          { required: true, trigger: 'blur', validator: validateUsername }
        ],
        phoneNum: [
          { required: true, trigger: 'blur', validator: validatePhone }
        ],
        verifyCode: [
          { required: true, trigger: 'blur', validator: validateCode }
        ],
        password: [
          { required: true, trigger: 'blur', validator: validatePassword }
        ]
      },
      passwordType: 'password',
      capsTooltip: false,
      clickSpaceCloseDialog: false,
      dialogTableVisible: false,
      modifyPasswordDialogVisible: false,
      modifyPhoneDialogVisible: false,
      modifyUserNameDialogVisible: false,
      modifyPortraitDialogVisible: false,
      portraitData: [],
      portrait: ''
    }
  },
  created () {
    this.messageOffset = screen.height * 0.4
    this.changePhoneBtText = ('换绑手机号')
    let header = getLoginInfo('url')
    if (header && header !== 'null') {
      this.userHeaderImg = getLoginInfo('url')
    }
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['系统设置', '修改密码'])
    obtainPortraitData('').then(res => { this.portraitData = res.data })
  },
  mounted () {
  },
  methods: {
    updateInfo (_this) {
      this.loading = true
      var params = {}
      params['id'] = JSON.parse(sessionStorage.getItem('userInfo')).id
      if (this.modifyPasswordDialogVisible) {
        params['password'] = _this.changePasswordDialogFormData.password
        params['user_name'] = _this.changePasswordDialogFormData.phoneNum
        params['code'] = String(_this.changePasswordDialogFormData.verifyCode)
      } else if (this.modifyPhoneDialogVisible) {
        params['user_name'] = _this.changePhoneDialogFormData.phoneNum
        params['code'] = String(_this.changePhoneDialogFormData.verifyCode)
      } else if (this.modifyUserNameDialogVisible) {
        params['real_name'] = _this.chnageNameDialogFormData.username
      } else if (this.modifyPortraitDialogVisible) {
        params['head_photo_id'] = this.portrait
      }
      console.log(params)
      updateUserInfo(params).then(data => {
        _this.loading = false
        if (data.status === 'success') {
          this.username = data.data.real_name
          this.userphone = data.data.user_name
          this.userHeaderImg = data.data.head_photo.url
          this.loginName = data.data.user_name
          if (this.modifyPasswordDialogVisible) {
            this.toastSuccess('修改密码成功!')
          } else if (this.modifyPhoneDialogVisible) {
            this.toastSuccess('换绑手机号成功!')
            saveLoginInfo('user_name', data.data.user_name)
          } else if (this.modifyUserNameDialogVisible) {
            saveLoginInfo('real_name', data.data.real_name)
            this.toastSuccess('修改用户名成功!')
            this.modifyUserNameDialogVisible = false
            event.$emit('updateUserName', data.data.real_name)
          } else if (this.modifyPortraitDialogVisible) {
            saveLoginInfo('url', data.data.head_photo.url)
            this.toastSuccess('修改用户头像成功!')
            this.modifyPortraitDialogVisible = false
            // 通知父级元素，返回图片路径
            event.$emit('updateUserHeader', data.data.head_photo.url)
          }
          this.reset()
          this.modifyPasswordDialogVisible = false
          this.modifyPhoneDialogVisible = false
          this.modifyUserNameDialogVisible = false
          this.modifyPortraitDialogVisible = false
        }
      }).catch(function (e) {
        _this.loading = false
      })
    },
    queryUserInfo () {
    },
    sentChangePhoneVerifyCode (_this) {
      var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
      if (!this.changePhoneDialogFormData.phoneNum) {
        this.sendVerifyCodeDisable = true
        this.toastWarning('请输入手机号')
        this.sendVerifyCodeDisable = true
      } else if (!reg.test(this.changePhoneDialogFormData.phoneNum)) {
        this.toastWarning('手机号格式不正确')
      } else {
        this.countDownTime = 60
        this.sendVerifyCodeDisable = false
        this.startCountTimer = true
        this.timer()
        // var params = {}
        // params['phone'] = this.changePhoneDialogFormData.phoneNum
        sentPhoneVerifyCode(this.changePhoneDialogFormData.phoneNum).then(data => {
          this.loading = false
          if (data.status === 'success') {
            if (data.data) {
              this.changePhoneDialogFormData.verifyCode = data.data
              this.sureButtonDisable = false
            }
            this.toastSuccess('发送验证码成功！')
          } else {
            this.loading = false
          }
        }).catch(function (e) {
          this.loading = false
        })
      }
    },
    sentChangePasswordVerifyCode (_this) {
      var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
      if (!this.changePasswordDialogFormData.phoneNum) {
        this.sendVerifyCodeDisable = true
        this.toastWarning('请输入手机号')
        this.sendVerifyCodeDisable = true
      } else if (!reg.test(this.changePasswordDialogFormData.phoneNum)) {
        this.toastWarning('手机号格式不正确')
      } else {
        this.countDownTime = 60
        this.sendVerifyCodeDisable = false
        this.startCountTimer = true
        this.timer()
        // var params = {}
        // params['phone'] = this.changePasswordDialogFormData.phoneNum
        sentPhoneVerifyCode(this.changePasswordDialogFormData.phoneNum).then(data => {
          this.loading = false
          if (data.status === 'success') {
            if (data.data) {
              // this.changePasswordDialogFormData.verifyCode = data.data
              this.sureButtonDisable = false
            }
            this.toastSuccess('发送验证码成功！')
          } else {
            this.loading = false
          }
        }).catch(function (e) {
          this.loading = false
        })
      }
    },
    // 覆盖默认上传行为
    uploadImg (item) {
      // let fd = new FormData()
      // fd.append('photo_id', item.file)
      // uploadImage(fd).then(res => {
      //   let params = {
      //     path: res.data.file_path,
      //     use: this.use
      //   }
      //   this.userHeaderImg = params.path
      //   this.updateInfo(this)
      // })
      let params = { 'upload_type': 'admin' }
      ossUpload(params).then(res => {
        let params = {
          host: res.data.host
        }
        let fd = new FormData()
        let postfix = item.file.name.split('.')[item.file.name.split('.').length - 1]
        let filename = this.$md5(`${new Date().getTime()}_${item.file.name}`)
        fd.append('callback', res.data.callback)
        fd.append('success_action_status', '200')
        fd.append('OSSAccessKeyId', res.data.accessid)
        fd.append('policy', res.data.policy)
        fd.append('signature', res.data.signature)
        fd.append('key', `${res.data.dir}${filename}.${postfix}`)
        fd.append('host', res.data.host)
        fd.append('file', item.file)
        fd.append('expire', res.data.expire)
        fd.append('name', `${filename}.${postfix}`)
        params['fd'] = fd
        uploadFile(params.host, params.fd).then(res => {
          // 更改图片路径
          this.userHeaderImg = res.filename
          this.relativeUserHeaderImg = res.filename
          this.updateInfo(this)
        }).catch(function (e) {
        })
      }).catch(function (e) {
      })
    },
    // 证件照上传前处理函数
    beforeAvatarUpload (file) {
      const isLt2M = file.size / 1024 / 1024 < 1
      if (!isLt2M) {
        this.$alert('上传头像图片大小不能超过 1MB!', '消息提示', {
          confirmButtonText: '确定',
          callback: action => { }
        })
      }
      return isLt2M
    },
    handleError () {
      this.$alert('头像上传失败！', '消息提示', {
        confirmButtonText: '确定',
        callback: action => { }
      })
    },
    onPhoneInput (value) {
      this.onInput(value)
      var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
      if (value && reg.test(value)) {
        this.sendVerifyCodeDisable = false
      } else {
        this.sendVerifyCodeDisable = true
      }
    },
    onInput (value) {
      if (this.modifyPasswordDialogVisible) {
        if (this.changePasswordDialogFormData.phoneNum && this.changePasswordDialogFormData.verifyCode && this.changePasswordDialogFormData.password) {
          this.sureButtonDisable = false
        }
      } else if (this.modifyPhoneDialogVisible) {
        if (this.changePhoneDialogFormData.phoneNum && this.changePhoneDialogFormData.verifyCode) {
          this.sureButtonDisable = false
        }
      } else if (this.modifyUserNameDialogVisible) {
        if (this.chnageNameDialogFormData.username) {
          this.sureButtonDisable = false
        }
      }
    },
    checkPhone (phone) {
      var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
      if (!reg.test(phone)) {
        this.sendVerifyCodeDisable = true
        this.toastWarning('手机号格式不正确')
      } else {
        this.sendVerifyCodeDisable = false
      }
    },
    timer: function () {
      if (this.startCountTimer && this.countDownTime > 0) {
        this.verifyCodeButtonTitle = this.countDownTime + 's后重新获取'
        this.sendVerifyCodeDisable = true
        this.countDownTime--
        var that = this
        setTimeout(that.timer, 1000)
      } else {
        this.countDownTime = 0
        this.verifyCodeButtonTitle = '获取验证码'
        this.sendVerifyCodeDisable = false
      }
    },
    toastSuccess (message) {
      this.$message({
        type: 'success',
        message: message,
        center: true,
        offset: this.messageOffset
      })
    },
    toastWarning (message) {
      this.$message({
        type: 'warning',
        message: message,
        center: true,
        offset: this.messageOffset
      })
    },
    beforeCloseDialog: function (done) {
      this.reset()
      done()
    },
    reset () {
      this.startCountTimer = false
      this.sendVerifyCodeDisable = true
      this.sureButtonDisable = true
      this.countDownTime = 60
      this.chnageNameDialogFormData.username = ''
      this.changePhoneDialogFormData.phoneNum = ''
      this.changePhoneDialogFormData.verifyCode = ''
      this.changePasswordDialogFormData.phoneNum = ''
      this.changePasswordDialogFormData.verifyCode = ''
      this.changePasswordDialogFormData.password = ''
      this.verifyCodeButtonTitle = '发送验证码'
      if (this.modifyPasswordDialogVisible) {
        this.$refs['changePasswordDialogFormData'].resetFields()
      } else if (this.modifyPhoneDialogVisible) {
        this.$refs['changePhoneDialogFormData'].resetFields()
      } else if (this.modifyUserNameDialogVisible) {
        this.$refs['chnageNameDialogFormData'].resetFields()
      }
    },
    checkCapslock () { },
    checkModifyUserName () {
      this.$refs.chnageNameDialogFormData.validate(valid => {
        if (valid) {
          this.updateInfo(this)
        } else {
          this.toastWarning('修改用户名失败!')
        }
      })
    },
    checkModifyPhone () {
      this.$refs.changePhoneDialogFormData.validate(valid => {
        if (valid) {
          this.updateInfo(this)
        } else {
          this.toastWarning('换绑手机号失败!')
        }
      })
    },
    checkModifyPassword () {
      this.$refs.changePasswordDialogFormData.validate(valid => {
        if (valid) {
          this.updateInfo(this)
        } else {
          this.toastWarning('修改密码失败!')
        }
      })
    },
    portraitClick (item) {
      console.log(item)
      this.portrait = item
    },
    updatePortrait () {
      this.userHeaderImg = this.portrait
      this.relativeUserHeaderImg = this.portrait
      this.updateInfo(this)
    }
  }
}
</script>

